.mm-btn_prev::before {
  left: 14px;
  background-image: svg-load('../blocks/mmenu/bg-img/arrow-circle-left-icon.svg', fill=$white);
}

.mm-listitem__text,
.mm-navbar__title {
  padding-left: 14px;
}

.mm-wrapper__blocker {
  display: none !important;
}

.mm-close {
  display: flex;
  justify-content: space-between;
  height: 80px;
  padding: 0 14px;

  img {
    display: block;
    margin: auto 0;
  }

  &__btn {
    width: 64px;
    height: 100%;
    background-color: $main-color;
    background-repeat: no-repeat;
    background-size: 26px;
    background-position: center;
    background-image: svg-load('../blocks/mmenu/bg-img/close-icon.svg', fill=$white);
  }
}

.mmenu {

  $block-name:                &; // #{$block-name}__element

}
